function init(){
    let out = '';
    for(let i = 0; i < keyboardEng.length; i ++){
        console.log(5);
        out+='<div class ="keyboard-key">' + keyboardEng[i] + '</div>';
    }
    document.querySelector('div').innerHTML = out;
}

